/**
 * WordPress dependencies.
 */
import {
	useBlockProps,
	useInnerBlocksProps,
	store as blockEditorStore,
	InspectorControls,
} from '@wordpress/block-editor';
import { useEffect } from '@wordpress/element';
import { useSelect, select } from '@wordpress/data';
import { BlockEditProps } from '@wordpress/blocks';

/**
 * External dependencies.
 */
import classnames from 'classnames';

/**
 * Internal dependencies.
 */
import Toolbar from './toolbar';
import { name as cellBlockName } from '../table-cell';
import { PanelBody } from '@wordpress/components';

/**
 * Edit function.
 *
 * @param {Object}   props               Edit properties.
 * @param {string}   props.className     Class name.
 * @param {string}   props.clientId      Client ID.
 * @param {Object}   props.attributes    Attributes.
 * @param {Function} props.setAttributes Set attributes.
 * @param {boolean}  props.isSelected    Is block selected.
 * @param {Object}   props.context       Block context.
 *
 * @return {JSX.Element} JSX Component.
 */
function TableColumnEdit( {
	className,
	clientId,
	attributes,
	setAttributes,
	isSelected,
	context,
}: BlockEditProps<any> ): JSX.Element {
	const blockProps = useBlockProps( {
		className: classnames( className, 'travelopia-table__column' ),
	} );

	const tableId: string = context[ 'travelopia/table-id' ] as string;
	const rowContainerType: string = context[ 'travelopia/table-row-container-type' ] as string;
	const rowContainerId: string = context[ 'travelopia/table-row-container-id' ] as string;

	const innerBlocksProps = useInnerBlocksProps(
		{
			...blockProps,
			colSpan: attributes.colSpan,
			rowSpan: attributes.rowSpan,
		},
		{
			template: [ [ cellBlockName ] ],
			templateLock: false,
		},
	);

	// Get the row and column index.
	const { row, column } = useSelect(
		( select: any ) => {
			// Calculate the row and column index.
			const columnIndex = select( blockEditorStore ).getBlockIndex( clientId );
			const rowClientId =
				select( blockEditorStore ).getBlockRootClientId( clientId );
			const rowIndex = select( blockEditorStore ).getBlockIndex( rowClientId );

			return {
				row: rowIndex + 1, // Start index at 1.
				column: columnIndex + 1,
			};
		},
		[ clientId ],
	);

	// Get the row and column index.
	useEffect(
		() => {
			if ( attributes.isRealCalculated ) {
				return;
			}
			console.log('calculating');
			// Calculate the row and column index.
			const columnIndex = select( 'core/block-editor' ).getBlockIndex( clientId );
			const rowClientId =
				select( 'core/block-editor' ).getBlockRootClientId( clientId );
			const rowIndex = select( 'core/block-editor' ).getBlockIndex( rowClientId ?? '' );

			setAttributes({ realRow: rowIndex + 1, realColumn: columnIndex + 1, isRealCalculated: true });
		},
		[],
	);

	// Update the row and column index.
	useEffect( () => {
		setAttributes( { row, column } );
	}, [ row, column, setAttributes ] );

	// Determine tag.
	let Tag: string = 'td';
	if ( 'tbody' !== rowContainerType ) {
		Tag = 'th';
	}

	return (
		<>
			<InspectorControls>
				<PanelBody title='Attributes'>
					<p>Row: { row }</p>
					<p>Column: { column }</p>
					<p>Rowspan: {attributes.rowSpan}</p>
					<p>Colspan: {attributes.colSpan}</p>
					<p>Client ID: {clientId}</p>
					<p>Real Row: {attributes.realRow}</p>
					<p>Real Column: {attributes.realColumn}</p>
				</PanelBody>
			</InspectorControls>
			<Toolbar
				isSelected={ isSelected }
				tableRow={ row }
				tableColumn={ column }
				tableId={ tableId }
				rowContainerId={ rowContainerId }
				realTableRow={ attributes.realRow }
				realTableColumn={ attributes.realColumn }
			/>
			<Tag
				{ ...innerBlocksProps }
			/>
		</>
	);
}

export default TableColumnEdit;
